.App {
  margin-top: 30px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#loading-progress {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fafafa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  overflow: hidden;
}

#loading-progress-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

#loading-logo {
  width: 25%;
}

#loading-logo img {
  width: 100%;
  height: 100%;
}

#loading-progress-bar {
  width: 512px;
  margin-top: 112px;
}

#progressContainer {
  border-radius: 2px;
  height: 4px;
  background: #f5f5f5;
}

#primaryProgress {
  background: #f47e30;
  border-radius: 2px;
  height: 4px;
  width: 0%;
  max-width: 100%;
  -webkit-animation: progress 0.5s 1 forwards;
          animation: progress 0.5s 1 forwards;
}

@-webkit-keyframes progress {
  from {}
  to {
    width: 100%
  }
}

@keyframes progress {
  from {}
  to {
    width: 100%
  }
}

#loading-progress-message {
  font-family: initial;
  font-size: 16px;
  line-height: initial;
  color: #9e9e9e;
  margin-top: 56px;
}

.jumbotron h1 {
  font-size: 35px;
}

.leftNav {
  padding-bottom: 10px;
}

.leftNav .btn-primary,
.leftNav .btn-primary:hover {
  border-color: #999999 !important;
  background-color: #f47e30 !important;
}

.btn-primary.mLinks {
  padding: 2px 5px;
  font-size: 1.1em;
}

.formContent label,
.radiolist {
  min-width: 175px;
  font-weight: normal;
  clear: both;
  margin: 5px 0;
}

.formContent select,
.formContent input,
.formContent textarea,
.formContent .inputdiv {
  display: initial;
  min-width: 50%;
  width: initial;
  margin: 5px 0;
}

label.required:after {
  content: " *";
  color: #ff0000;
}

.embed .modal-header {
  background-color: #409ca9;
  color: white;
}

.embed .modal-header .close {
  color: white;
}

.embed .modal-body {
  max-height: calc(100vh - 200px);
  overflow-y: auto;
  padding: 25px;
}

.embed .modal-footer {
  background-color: lightgray;
}

.installation-modal .modal-header {
  background-color: #409ca9;
  color: white;
}

.installation-modal .modal-header .close {
  color: white;
}

.installation-modal .modal-body {
  max-height: calc(100vh - 100px);
  overflow-y: auto;
}

.installation-modal .modal-footer {
  background-color: lightgray;
}


