.availableVideoSearch {
    height: 65px;
}

.playlistOrder {
    height: 65px;
}

.availableVideos, .playlistVideos {
    overflow-y: scroll;
    height: 250px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
}

.video {
    cursor: pointer;
    clear: both;
    border-bottom: 1px solid #ccc;
}

.videoSelected {
    background-color: #a5c13f;
}

.overButtons {
    text-align: center;
}

.btn-space {
    margin-right: 5px;
}

.resultImage {
    padding: 5px;
}


