.drag-videos-icon {
    padding-top: 50px;
}

.drag-videos-target {
    border: medium none;
    display: inline-block;
    margin: 1px 0 2px;
    outline: medium none;
    padding: 0.75em 1em 0.6em;
    position: relative;
    vertical-align: middle;
}

.drag-or-label {
    margin-top: 0px;
    margin-bottom: 20px;
}

.dropzone {
    width: 100%;
    height: 300px;
    border-width: 2px;
    border-color: #bbb;
    border-style: dashed;
    border-radius: 5px;
}


